body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.image-content {
  display: inline-block;
  margin: 5px;
  border: 1px solid #ccc;
}
.image-content .image-box {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  width: 100px;
  height: 80px;
  border-bottom: 1px solid #ccc;
}
.image-content .image-box .box-image {
  max-width: 100%;
  max-height: 100%;
}
.image-content .image-text {
  width: 100px;
  height: 30px;
  line-height: 30px;
  white-space: nowrap;
  font-size: 12px;
  overflow: hidden;
}
.image-content-active {
  border: 1px solid #38f;
}

.edit-modal .image-content {
  display: inline-block;
  margin: 5px;
  border: 1px solid #ccc;
  cursor: pointer;
}
.edit-modal .image-content .image-box {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  width: 100px;
  height: 80px;
  border-bottom: 1px solid #ccc;
}
.edit-modal .image-content .image-box .box-image {
  max-width: 100%;
  max-height: 100%;
}
.edit-modal .image-content .image-text {
  width: 100px;
  height: 30px;
  line-height: 30px;
  white-space: nowrap;
  font-size: 12px;
  overflow: hidden;
}

.App {
  text-align: center;
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-link {
  color: #61dafb;
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.config-operation span {
  margin-right: 10px;
  color: #38f;
  cursor: pointer;
}
.config-operation span:last-child {
  margin-right: 0;
}
.image-operation .image-preview {
  width: 100px;
}
.login-form {
  width: 400px;
}
.login-form .login-item {
  margin-bottom: 10px;
}
.login-form .login-item .item-name {
  display: inline-block;
  width: 80px;
}
.login-form .login-item .item-value {
  display: inline-block;
  width: 300px;
}

